.vux-tab-ink-bar {
  position: absolute;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: white;
  text-align: center;
}
.vux-tab-ink-bar-transition-forward {
  -webkit-transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1), left 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s;
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1), left 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s;
}
.vux-tab-ink-bar-transition-backward {
  -webkit-transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s, left 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  transition: right 0.3s cubic-bezier(0.35, 0, 0.25, 1) 0.09s, left 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}
.vux-tab-bar-top .vux-tab-ink-bar {
  top: 0;
}
.vux-tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #c71717;
  height: 44px;
  position: relative;
}
.vux-tab button {
  padding: 0;
  border: 0;
  outline: 0;
  background: 0 0;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
.vux-tab .vux-tab-item {
  display: block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-size: 100% 1px;
  font-size: 0.45rem;
  text-align: center;
  font-weight: 500;
  line-height: 44px;
}
.vux-tab .vux-tab-item.vux-tab-selected {
  color: white;
  border-bottom: 3px solid white;
}
.vux-tab-bar-top .vux-tab .vux-tab-item {
  background: -webkit-gradient(linear, left top, left bottom, from(#e5e5e5), color-stop(#e5e5e5), to(rgba(229, 229, 229, 0))) top left no-repeat;
  background: linear-gradient(180deg, #e5e5e5, #e5e5e5, rgba(229, 229, 229, 0)) top left no-repeat;
  background-size: 100% 1px;
}
.vux-tab-bar-top .vux-tab .vux-tab-item.vux-tab-selected {
  border-bottom: none;
  border-top: 3px solid #04BE02;
}
.vux-tab .vux-tab-item.vux-tab-disabled {
  color: #ddd;
}
.vux-tab.vux-tab-no-animate .vux-tab-item.vux-tab-selected {
  background: 0 0;
}
/** when=prop:custom-bar-width **/
.vux-tab-bar-inner {
  display: block;
  background-color: #04BE02;
  margin: auto;
  height: 100%;
  -webkit-transition: width 0.3s cubic-bezier(0.35, 0, 0.25, 1);
  transition: width 0.3s cubic-bezier(0.35, 0, 0.25, 1);
}
.vux-tab-item-badge {
  position: absolute;
  top: 0;
  bottom: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  height: 18px;
  min-width: 18px;
  padding: 0 4px;
  border-radius: 30px;
  margin: auto 0 auto 4px;
  line-height: 18px;
  font-size: 11px;
  background-clip: padding-box;
  vertical-align: middle;
}
.vux-tab-wrap {
  position: relative;
  padding-top: 44px;
  top: 0.8rem;
}
.vux-tab-container {
  height: 44px;
  top: 0;
  left: 0;
  right: 0;
  /*overflow: hidden;*/
  position: absolute;
}
.scrollable {
  overflow-y: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 17px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
.scrollable::-webkit-scrollbar {
  display: none;
}
.scrollable .vux-tab-ink-bar {
  bottom: 17px;
  position: absolute;
}
.scrollable .vux-tab-item {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 22%;
          flex: 0 0 22%;
}

#personal-battle {
  height: 100%;
  overflow: hidden;
  font-size: 0.4rem;
}
.back-arrow {
  position: absolute;
  left: 0.5rem;
  top: 0.5rem;
}
.medal {
  width: 0.5rem;
  height: 0.5rem;
  vertical-align: middle;
}
#user-info {
  height: 10%;
  padding-top: 1.5rem;
  padding-bottom: 0.5rem;
  background-image: linear-gradient(-20deg, #ddd6f3 0%, #faaca8 100%, #faaca8 100%);
  -webkit-box-shadow: 0 2px 5px #aaaaaa;
          box-shadow: 0 2px 5px #aaaaaa;
}
#user-info #portrait,
#user-info #data {
  display: inline-block;
  vertical-align: middle;
}
#user-info .portrait {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.5rem;
  border-radius: 50%;
  border: 0.05rem solid white;
  overflow: hidden;
}
#user-info #data {
  text-align: left;
}
#user-info #name {
  font-size: 0.5rem;
  font-weight: bold;
}
#user-info .honor {
  color: #e74c3c;
  vertical-align: middle;
}
#user-info .win-rate {
  color: #2980b9;
  vertical-align: middle;
}
#filter {
  padding: 0.5rem 0;
  height: 5%;
}
#filter .filter-button {
  display: inline-block;
  width: 20%;
  height: 0.8rem;
  line-height: 0.8rem;
  background: #bbbbbb;
  border-radius: 0.1rem;
  margin: 0.1rem;
  color: white;
  font-size: 0.4rem;
}
#filter .selected {
  background: #e74c3c;
}
#record-list {
  padding: 0 0.3rem;
  height: 12rem;
  overflow: scroll;
}
